/* ==========================================================================
   WooCommerce Cart page styles - begin
   ========================================================================== */
.eltdf-woocommerce-page {
	.woocommerce {
		table.cart {

			thead {

				tr {
					border: 0;
					border-bottom: 0 solid $default-border-color;

					th {
						padding: 10px 0;
						color: $default-heading-color;
						text-align: left;
					}
				}
			}

			tbody {

				tr {
					border: 0;
				}
			}

			tr.cart_item {
				border-bottom: 1px solid #e6e6e6;

				td {
					padding: 25px 0;
					vertical-align: middle;
					text-align: inherit;

					&.product-quantity{
						text-align: center;
					}

					&.product-remove {
						padding-left: 10px;
						padding-right: 10px;
						text-align: right;

						a {
							color: $default-text-color;
							font-size: 0;
							line-height: 2;

							&:after {
								content: "\4d";
								display: inline-block;
								vertical-align: middle;
								speak: none;
								color: $default-heading-color;
								font-family: 'ElegantIcons';
								font-size: 24px;
								line-height: inherit;
								font-style: normal;
								font-weight: normal;
								font-variant: normal;
								text-transform: none;
								text-rendering: auto;
								-webkit-font-smoothing: antialiased;
								-moz-osx-font-smoothing: grayscale;
							}

							&:hover {
								color: $default-heading-color;
							}
						}
					}

					&.product-thumbnail {
						width: 116px;
						padding-right: 20px;

						a, img {
							display: block;
						}
					}

					.variation {

						> * {
							display: inline-block;
							vertical-align: middle;
							padding: 0;
							margin: 0;
						}

						p {
							margin: 0;
						}
					}

					.eltdf-quantity-buttons{

						&:before{
							content:'Quantity';
							font-size: 14px;
							color: #464646;
							font-family: $default-text-font;
							position: absolute;
							top: 50%;
							left: 10%;
							@include eltdfTransform(translateY(-50%));
						}
					}

					.eltdf-quantity-buttons .eltdf-quantity-input {
						width: 195px;
						margin: 0;
						text-align: center;
						padding-left: 73%;
						padding-right: 20px !important;
						font-family: $default-text-font;
						color: #464646;
						font-weight: 300;
						font-size: 12px;

						@media screen and (max-width: 768px){
							padding-left: 75%;
						}
					}

					.eltdf-quantity-buttons .eltdf-quantity-minus {
						bottom: 0;
						top: 0;
						right: 39px;
						position: absolute;
						border: 0;
						z-index: 100;
						height: 100%;

						&:before {
							content: "\34";
							font-size: 18px;
							top: 25%;
							position: relative;
						}
					}

					.eltdf-quantity-buttons .eltdf-quantity-plus {
						bottom: 0;
						top: 0;
						right: 12px;
						position: absolute;
						border: 0;
						z-index: 100;
						height: 100%;

						&:before {
							content: "\35";
							font-size: 18px;
							top: 25%;
							position: relative;
						}
					}
				}

				.product-name {
					font-family: $default-heading-font;
					font-size: 15px;
					font-weight: 500;
					padding-right: 30px;
				}

				.product-price {
					font-family: $default-heading-font;
					font-size: 14px;
					font-weight: 400;
					color: #2d2d2d;
				}

			}

			td.actions {
				padding: 35px 0 36px;
				text-align: inherit;

				button[type=submit] {
					background-color: transparent;
					color: #000;
					float: right;
					margin-top: 23px;
					padding: 0;
					border: none;
					@include eltdfAnimationLineVisible($bottom: 2px, $color: currentColor);

					&:hover{
						@include eltdfAnimationLineHidden($bottom: 2px, $color: currentColor);
					}
				}

				input[type=submit] {
					font-family: "dripicons-v2" !important;
					content: attr(data-icon);
					font-style: normal !important;
					font-weight: normal !important;
					font-variant: normal !important;
					text-transform: none !important;
					speak: none;
					line-height: 1;
					-webkit-font-smoothing: antialiased;
					-moz-osx-font-smoothing: grayscale;
					font-size: 18px;
					background-color: #fff;
					border-color: transparent;
					color: #000;
					padding: 0;
					position: absolute;
					right: 20px;
					bottom: 10px;
				}

				.coupon {
					position: relative;
					display: inline-block;

					label {
						display: none;
					}

					input[type="text"] {
						margin: 0 22px 0 0;
						padding: 18px 14px 8px 27px;
						width: 380px;
						font-family: $default-text-font;
                        color: $default-heading-color;
                        font-size: 10px;
                        font-weight: 400;
                        text-transform: uppercase;
                        letter-spacing: .16em;
					}

					.eltdf-coupon-icon {
						position: absolute;
						left: 0;
						bottom: 14px;
						font-size: 15px;
						line-height: 1em;
						color: $default-heading-color;
					}
				}

				> input {
					float: right;
				}

				.eltdf-post-read-more-button {
					margin-top: 26px;

					.eltdf-btn.eltdf-btn-simple {
						text-transform: capitalize;
					}
				}
			}
		}

		h2 {
			font-size: 25px;
			margin-bottom: 21px;
			text-transform: capitalize;
		}

		.woocommerce-cart-form {
			width: 65%;
			float: left;
			padding-right: 58px;
			box-sizing: border-box;

			@media screen and (max-width: 1200px){
				width: 100%;
			}

			h2 {
				padding-top: 23px;
			}

			> table > thead {
				display: none;
			}
		}

		.cart-collaterals {
			position: relative;
			float: right;
			margin: 110px 0;
			width: 35%;
			padding: 0;
			box-sizing: border-box;
			background-color: #f4f4f4;

			@media screen and (max-width: 1200px){
				width: 100%;
			}

			h2 {
				position: absolute;
				top: -63px;
				display: block;
				margin: 0 0 26px -28px;
			}

			table {
				margin: 0 0 16px;

				tr {
					border: 0;
					border-bottom: 0 solid $default-border-color;
					vertical-align: baseline;
				}

				tr.shipping {
					td {
						font-size: 14px;
						line-height: 32px;
					}
				}

				td, th {
					padding: 7px 0;
					text-align: left;
					vertical-align: middle;
				}

				td[data-title="Total"] {
					text-align: right;
					padding-right: 20px;
					font-family: $default-heading-font;
					font-size: 18px;
					font-weight: 700;
					color: $default-heading-color;
				}

				th {
					width: 20%;
					padding: 10px 28px 10px 0;
					border: 0;
					vertical-align: baseline;
					color: $default-heading-color;
					font-size: 18px;
					font-family: $default-heading-font;
					font-weight: 600;
				}
			}

			tr.shipping {

				#shipping_method {
					list-style: none;
					margin: 0 0 7px;

					li {
						margin: 3px 0;
						padding: 0;

						> * {
							display: inline-block;
							vertical-align: middle;
							margin: 0;
						}
                        > label {
                            padding-left: 6px;
                        }
					}
				}
			}

			.woocommerce-shipping-calculator {

				.shipping-calculator-button {
					display: block;
					margin: 24px 0 20px;
					font-size: 18px;
					font-weight: 600;
					font-family: $default-heading-font;
					text-transform: capitalize;

					&:after {
						content: "\f107";
						position: absolute;
						display: inline-block;
						vertical-align: top;
						font-family: "Font Awesome 5 Free";
						line-height: inherit;
						-webkit-font-smoothing: antialiased;
						-moz-osx-font-smoothing: grayscale;
						margin-left: 10px;
					}
				}

				p {
					margin: 0 0 10px 0;

					> button {
						margin: 3px 0 27px 0;
						width: 100%;
						font-size: 14px;
					}
				}

				input{
					padding: 5px 0;
					margin-bottom: 18px;
                    color: $default-heading-color;
                    font-size: 10px;
                    font-weight: 400;
                    text-transform: uppercase;
                    letter-spacing: .16em;
				}

				.select2-container--default {
					text-align: left !important;
					border-bottom: 1px solid $default-border-color !important;

					.select2-selection--single .select2-selection__arrow {

						right: 0;

						&:before {
							font-family: "linea-arrows-10";
							font-size: 13px;
							content: "\e019";
						}
					}
				}
			}
		}

		.cross-sells {
			margin: 30px 0 0;
			border-top: 1px solid $default-border-color;

			> h2 {
				display: block;
				margin: 36px 0 50px;
				text-align: center;
			}
		}

		.cart_totals {
			margin: 24px 28px 40px;

			.wc-proceed-to-checkout {

				a {
					width: 100%;
					text-align: center;
					font-size: 14px;
				}
			}

			.select2-container--default .select2-selection--single .select2-selection__rendered {
				padding: 0 30px 0 0;
				text-transform: capitalize;
				font-size: 14px;
				letter-spacing: 0;
			}
		}
	}


	.cart-empty {
		position: relative;
		display: block;
		text-align: center;
		text-transform: capitalize;
		margin: 46px 0 33px;
		font-size: 38px;
		line-height: 1.1em;
		color: $default-heading-color;
		font-weight: 600;
		font-family: $default-heading-font;
	}

	.return-to-shop {
		position: relative;
		display: block;
		text-align: center;
		margin: 0 0 40px;
	}
}

.eltdf-woocommerce-page {
    .eltdf-page-content-holder:not(:last-child) {
        .woocommerce {
            .woocommerce-cart-form {
                width: 100%;
            }

            .cart-collaterals {
                width: 100%;
            }
        }
    }

}

/* ==========================================================================
   WooCommerce Cart page styles - end
   ========================================================================== */