/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltdf-pl-holder {
    @include eltdfRelativeHolderLayout();

    .eltdf-pl-outer.eltdf-ajax-loading {
        opacity: 0;
    }
	
	&.eltdf-enable-rand-fadein {
		.eltdf-pli {
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				width: 100%;
				background-color: #fff;
			}
			
			@include keyframes(eltdfFadeOutCover) {
				0% {
					opacity: 1;
				}
				
				100% {
					opacity: 0;
				}
			}
			
			&.eltdf-fade-out-cover {
				&:after {
					@include eltdfAnimation(eltdfFadeOutCover 1s cubic-bezier(.645,.045,.355,1) forwards);
				}
			}
			
			&.eltdf-remove-cover {
				&:after {
					display: none;
				}
			}
		}
	}

    .eltdf-prl-loading {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 101;
        background-color: transparent;
        display: none;
        margin-top: 45px;
        box-sizing: border-box;
        text-align: center;
        padding: 120px;

        .eltdf-prl-loading-msg {
            font-size: 12px;
            line-height: 1.3em;
            color: $default-heading-color;
            font-weight: 500;
        }
    }

    .eltdf-pli {
        padding: 0;
        z-index: 100;
	
	    .eltdf-pli-icons-holder {
		    @include eltdfInlineFlexContainer(null, null, row-reverse);
		    padding: 0 8px;
		
		    > .eltdf-pli-add-to-cart a.button,
		    > .yith-quickview-button,
		    > .yith-wcwl-add-to-wishlist,
		    .added_to_cart {
			    height: 43px;
			    width: 18px;
			    z-index: 300;
			    overflow: hidden;
			    padding: 0 8px;
			    font-size: 0;
			    vertical-align: bottom;
			    margin: 0;
			    border: 0;
			    box-sizing: initial;
			    @include eltdfTransition(opacity .2s ease-out);
			
			    &:before, &:after {
				    content: initial;
				    color: inherit;
			    }
			    
			    &:hover {
				    opacity: .8;
			    }
		    }
		
		    > .eltdf-pli-add-to-cart {
			    a.button {
				    bottom: 0;
				    right: -1px;
				    display: inline-block;
				    background: none;
				    color: inherit;
				
				    &.added {
					    display: none;
				    }
				
				    &.add_to_cart_button {
					    box-sizing: content-box;
					    
					    &:after {
						    font-family: 'Ionicons';
						    content: "\f110";
						    font-size: 19px;
						    line-height: 42px;
						    font-weight: 500;
					    }
				    }
			    }
			
			    .added_to_cart {
				    color: unset;
				    background-color: unset;
				    font-weight: unset;
				    
				    &:after {
					    content: "Z";
					    font-size: 18px;
					    line-height: 46px;
					    font-family: 'ElegantIcons';
				    }
			    }
		    }
		
		    > .yith-quickview-button {
			    bottom: 0;
			
			    &:before {
				    content: "\f06e";
				    font-size: 17px;
				    line-height: 43px;
				    font-family: 'FontAwesome';
			    }
		    }
		
		    > .yith-wcwl-add-to-wishlist {
			    bottom: 0;
			
			    > div {
				    height: 100%;
				
				    a {
					    height: 100%;
					
					    &:before {
						    font-size: 16px;
						    line-height: 45px;
						    padding: 0;
						    margin: 0;
					    }
				    }
			    }
		    }
	    }

        &:hover {
	        .eltdf-pli-image {
		        img {
			        @include eltdfFlickerFix();
			        @include eltdfTransform(translate(-3%,0) scale(1.06));
			        @include eltdfTransitionTransform(.75s cubic-bezier(.33,.02,0,.93))
		        }
	        }
        }

        .eltdf-pli-title {
            margin: 0;
            > * {
                font-family: inherit;
            }
        }

        .eltdf-pli-category {
	        font-family: $default-text-font;
	        display: block;
            font-size: 10px;
            text-transform: uppercase;
            font-weight: 500;
            letter-spacing: .160em;
            line-height: 21px;
            margin-top: 3px;
        }

        .eltdf-pli-price {
            font-size: 14px;
            margin: 0;
            line-height: 21px;
        }

        .eltdf-pli-price {
            @include eltdfWooCommercePriceStyle();
        }

        .eltdf-pli-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 0px 0 0;
        }

        .eltdf-pli-rating {
            @include eltdfWooCommerceRatingsStyle();
        }

        .eltdf-pli-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 11px 0 0;
            display: inline-block;
            font-size: 14px;
        }
    }
	
	&:not(.eltdf-product-add-to-cart-in-box):not(.eltdf-info-below-image) {
		.eltdf-pli-add-to-cart {
			margin: 0;
			padding: 0 8px;
		}
		
		.button,
		.added_to_cart {
			@include eltdfButtonDefaultStyle();
			@include eltdfAnimationLineVisible($color: currentColor, $bottom:2px);
			
			padding: 0 !important;
			color: $default-text-color;
			background-color: transparent;
			border: 0;
			letter-spacing: 0.01em;
			vertical-align: middle;
			
			> span {
				@include eltdfAnimationLineVisible($bottom:2px, $color: currentColor);
			}
			
			&:hover {
				background-color: transparent;
				color: $default-text-color;
				@include eltdfAnimationLineHidden($color: currentColor, $bottom:2px);
				
				> span {
					@include eltdfAnimationLineHidden($bottom:2px);
				}
				
			}
			
			.eltdf-btn-text {
				display: inline-block;
				vertical-align: middle;
			}
			
			white-space: nowrap;
			z-index: 3;
		}
		
		.added_to_cart {
			font-size: 0;
			
			&:before {
				content:none;
			}
		}
		
		.button.added {
			display: none;
		}
	}
	
	&.eltdf-hover-type-standard-dark .eltdf-pli-icons-holder {
		background: #000 !important;
		
		.yith-quickview-button,
		.yith-wcwl-add-button,
		.yith-wcwl-wishlistaddedbrowse a:before,
		.yith-wcwl-wishlistexistsbrowse a:before,
		.yith-wcwl-add-to-wishlist *,
		.eltdf-pli-add-to-cart a.button {
			color:#fff !important;
		}
		.added_to_cart {
			color: #fff !important;
			font-size: 0;
			
			&:before {
				content:initial;
			}
			
			&:after {
				content: "Z";
				font-size: 18px;
				line-height: 46px;
				font-family: 'ElegantIcons';
			}
		}
		
		&:hover {
			opacity: .9;
		}
	}

    .eltdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .eltdf-pli-image {
            @include eltdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
	            backface-visibility: hidden;
	            @include eltdfTransform(scale(1.06) rotate(0.1deg)); //The 0.1deg rotation fixes the jitter in Firefox
	            @include eltdfTransitionTransform(.45s cubic-bezier(.33,.02,0,.93));
            }

            .eltdf-pli-onsale,
            .eltdf-pli-out-of-stock,
            .eltdf-pli-new-product {
                @include eltdfWooCommerceMarksGeneralStyle();
            }

            .eltdf-pli-onsale {
                @include eltdfWooCommerceSaleStyle();
            }

            .eltdf-pli-out-of-stock {
                @include eltdfWooCommerceOutOfStockStyle();
            }

            .eltdf-pli-new-product {
                @include eltdfWooCommerceNewProductStyle();
            }
        }

        .eltdf-pli-text {
            @include eltdfAbsoluteHolderLayout();
	        right: 0;
            padding: 10px;
            box-sizing: border-box;
        }

        .eltdf-pli-text-outer {
            @include eltdfTableLayout();
            text-align: center;

            .eltdf-pli-text-inner {
	            display: table-cell;

            }
        }

        .eltdf-pli-link {
            @include eltdfAbsoluteHolderLayout();
        }
    }

    .eltdf-pli-text-wrapper {
        @include eltdfRelativeHolderLayout();
        margin: 23px 0 38px;
    }

    /*************** Masonry Layout - begin ***************/

    &.eltdf-masonry-layout {

        .eltdf-pli-inner {
            width: 100%;
            height: 100%;

            .eltdf-pli-image {
                height: 100%;
            }
        }
    }

    /*************** Masonry Layout - end ***************/

    /*************** Carousel Layout - begin ***************/

    &.eltdf-carousel-layout {

        &.eltdf-navigation-inside-slider {

            .eltdf-owl-slider .owl-nav {
                .owl-next,
                .owl-prev {
                    background-color: transparent;
                    top: 50%;
                    bottom: auto;
					@include eltdfTransform(translateY(-50%));

                    @include ipad-landscape {
                        background-color: #fff;
                    }
                }

                .owl-prev {
                    left: -83px;

                    @include ipad-landscape {
                        left: 0;
                    }
                }

                .owl-next {
                    right: -83px;

                    @include ipad-landscape {
                        right: 0;
                    }
                }
            }

            .eltdf-pl-outer {

                .eltdf-pli {
                    margin: 0 !important;
                }
            }
        }

        .eltdf-pl-outer {
            margin: 0 !important;

            .eltdf-pli {
                padding: 0 !important;
                width: auto;
            }
        }

        @media only screen and (max-width: 991px) {
            .eltdf-with-custom-holder {
                position: relative;
                left: 0 !important;
                top: 0px !important;
            }
        }

    }

    &.eltdf-carousel-layout &:not(.eltdf-info-outside-with-custom-text) {

        .owl-nav {

            .owl-prev,
            .owl-next {
                top: calc(50% - 123px);
                background-color: transparent;
            }

            .owl-prev {
                left: -88px;
                right: initial;
            }

            .owl-next {
                right: -88px;
            }
        }
    }

    /*************** Carousel Layout - end ***************/

    &.eltdf-info-on-image {
	    
        .eltdf-pli {
	
	        .eltdf-pli-icons-holder {
		        position: relative;
		        z-index: 111;
		        opacity: 0;
		        border: 1px solid;
		        margin: 23px auto 0;
		        @include eltdfFlickerFix();
		        @include eltdfTransition(opacity 0.2s ease-out);
	        }
	
	        &:hover {
		        .eltdf-pli-icons-holder {
			        opacity: 1;
		        }
		
		        .eltdf-pli-inner .eltdf-pli-text {
			        opacity: 1;
		        }
	        }

            .eltdf-pli-inner {

                .eltdf-pli-text {
                    padding: 30px;
                    background-color: rgba(255, 255, 255, 0.9);
                    opacity: 0;
                    @include eltdfTransition(opacity .66s cubic-bezier(0.76, 0.14, 0, 0.74));
	                
	                .eltdf-pli-price {
		                margin-top: 16px;
	                }
                }
            }
        }

        &.eltdf-product-vertical-top {
            .eltdf-pli .eltdf-pli-text-inner {
                vertical-align: top;
            }
        }

        &.eltdf-product-vertical-middle {
            .eltdf-pli .eltdf-pli-text-inner {
                vertical-align: middle;
            }
        }

        &.eltdf-product-vertical-bottom {
            .eltdf-pli .eltdf-pli-text-inner {
                vertical-align: bottom;
            }
        }

        &.eltdf-product-horizontal-left {
            .eltdf-pli .eltdf-pli-text-inner {
                text-align: left;
            }
        }

        &.eltdf-product-horizontal-center {
            .eltdf-pli .eltdf-pli-text-inner {
                text-align: center;
            }
        }

        &.eltdf-product-horizontal-right {
            .eltdf-pli .eltdf-pli-text-inner {
                text-align: right;
            }
        }

        &.eltdf-product-info-light {

            .eltdf-pli-inner {

                .eltdf-pli-text-inner {

                    .eltdf-pli-title,
                    .eltdf-pli-category a,
                    .eltdf-pli-excerpt,
                    .eltdf-pli-rating,
                    .eltdf-pli-price {
                        color: #fff;
                    }

                    .eltdf-pli-category a:hover {
                        color: rgba(#fff, .8);
                    }
                }
            }
        }

        &.eltdf-product-info-dark {

            .eltdf-pli-inner {

                .eltdf-pli-text-inner {

                    .eltdf-pli-title,
                    .eltdf-pli-category a,
                    .eltdf-pli-excerpt,
                    .eltdf-pli-rating,
                    .eltdf-pli-price {
                        color: $default-heading-color;
                    }

                    .eltdf-pli-category a:hover {
                        color: rgba($default-heading-color, .8);
                    }
                }
            }
        }
    }

    &.eltdf-info-below-image {

        .eltdf-pli {

            .eltdf-pli-title {
                float: left;
                width: 55%;
            }

            .eltdf-pli-category {
                font-size: 10px;
                text-transform: uppercase;
                font-weight: 500;
                letter-spacing: .160em;
                line-height: 21px;
                margin: 0;
                width: 100%;
                display: inline-block;
            }

            .eltdf-pli-price {
                float: right;
                width: 45%;
                text-align: right;
                margin: 4px 0 0;
            }

            &:hover .eltdf-pli-inner {

                .eltdf-pli-text {
	                
	                .eltdf-pli-icons-holder {
		                @include eltdfTransform(translateX(0));
	                }
                }
            }

            .eltdf-pli-inner {

                .eltdf-pli-text {
                    padding: 0;
	                
	                .eltdf-pli-icons-holder {
		                position: absolute;
		                bottom: -1px;
		                right: -1px;
		                background-color: #fff;
		                z-index: 111;
		                @include eltdfTransform(translateX(100%));
		                @include eltdfTransition(transform .35s cubic-bezier(0.34, 0.18, 0, 0.9));
		
	                }
                }
            }
        }

        &.eltdf-product-info-align-left,
        &.eltdf-product-info-align-center,
        &.eltdf-product-info-align-right {
            .eltdf-pli-text-wrapper > * {
                width: 100%;
            }

            .eltdf-pli-price {
                text-align: inherit;
            }
        }

        &.eltdf-product-info-align-center {
            .eltdf-pli-price {
                margin: 13px 0 0;
            }
        }
    }
	
	&.eltdf-info-below-image-icons-on {
		
		.eltdf-pli {
			.eltdf-pli-title {
				float: left;
				width: 55%;
			}
			
			.eltdf-pli-category {
				font-size: 10px;
				text-transform: uppercase;
				font-weight: 500;
				letter-spacing: .160em;
				line-height: 21px;
				margin: 0;
				width: 100%;
				display: inline-block;
			}
			
			.eltdf-pli-price {
				float: right;
				width: 45%;
				text-align: right;
				margin: 4px 0 0;
			}
			
			&:hover .eltdf-pli-inner {
				.eltdf-pli-text {
					opacity: 1;
				}
			}
			
			.eltdf-pli-inner {
				
				.eltdf-pli-text {
					padding: 0;
					background-color: rgba(255, 255, 255, 0.9);
					opacity: 0;
					@include eltdfTransition(opacity .4s cubic-bezier(0.76, 0.14, 0, 0.74));
					
					.eltdf-pli-icons-holder {
						position: absolute;
						top: 50%;
						left: 50%;
						background-color: #fff;
						z-index: 111;
						@include eltdfFlickerFix();
						@include eltdfTransform(translate(-50%,-50%));
					}
				}
			}
		}
		
		&.eltdf-product-info-align-left,
		&.eltdf-product-info-align-center,
		&.eltdf-product-info-align-right {
			.eltdf-pli-text-wrapper > * {
				width: 100%;
			}
			
			.eltdf-pli-price {
				text-align: inherit;
			}
		}
		
		&.eltdf-product-info-align-center {
			.eltdf-pli-price {
				margin: 13px 0 0;
			}
		}
	}

    &.eltdf-info-outside-with-custom-text {

        .eltdf-pli-onsale,
        .eltdf-pli-out-of-stock,
        .eltdf-pli-new-product {
            display: none !important;
        }

        .owl-nav {

            .owl-prev,
            .owl-next {
                width: 40px;
                height: 40px;
                line-height: 40px;
            }

            .owl-prev {
                right: 40px;
            }
        }

        .eltdf-pli {
            margin: 0 !important;

            .eltdf-pli-inner {

                .eltdf-pli-text {

                    .eltdf-pli-price{
                        position: absolute;
                        top: 10px;
                        left: 11px;
                        margin: 0;
						font-size: 16px;
						font-family: $default-text-font;
						font-weight: 500;

						.woocommerce-Price-currencySymbol{
							font-size: 10px;
                            vertical-align: top;
                            margin: 0 2px 0;
						}
                    }
                }
            }

        }
    }

    .eltdf-with-custom-text{
        font-family: $default-text-font;
        font-weight: 400;
        font-size: 12px;
        text-transform: uppercase;
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 40px;
        line-height: 40px;
        margin: 0;
        padding: 0;
        color: #000;
        letter-spacing: .42em;
        text-align: left;
        z-index: 100;
        box-sizing: border-box;
        transform-origin: 0 0;
        @include eltdfTransform(rotate(90deg) translateZ(0));
    }

    &.custom-text-position-right{
        .eltdf-with-custom-text{
            left: calc(100% + 40px);
        }
    }

    .eltdf-with-custom-holder{
        font-size:0;
        line-height: 0;
        position: absolute;
        display: block;
        top: 25%;
        left: 94%;
        z-index: 100;
        width: 50%;
        @include eltdfTransform(translateY(-50%));

        @include ipad-portrait {
            left: 10% !important;
        }

		@include phone-portrait {
			width: 75%;
		}
    }

    .eltdf-with-custom-title{
        font-family: $default-heading-font;
        font-size: 25px;
        line-height: 28px;
        font-weight: 600;
        display: block;
    }

    .eltdf-with-custom-link{
        position: relative;
    }

    .eltdf-with-custom-button{
        margin-top: 25px;
        font-family: $default-heading-font;
        font-size: 13px;
        font-weight: 600;
        display: block;
    }
}

.eltdf-drop-down .eltdf-pli-icons-holder {
	display: none !important;
}

/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */