/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 72px;
    $inner-padding: 20px; // distance between image and content
    $widget-list-item-padding: 0; // same as sidebar vars

    clear: both;

    li {
        position: relative;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        font-family: $default-heading-font;
        font-weight: 500;
        font-size: 15px;
        line-height: 26px;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        @include eltdfWooCommercePriceStyle();
        font-size: 13px;
        line-height: 24px;
        margin: 0;

        span { // currency symbol
            color: inherit;
        }
    }

    del{
        display: block;
        margin-right: 8px;
        color: rgba($second-heading-color, .3);

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        display: block;

        // currency symbol
        span {
            color: inherit;
        }
    }

    ins {
        text-decoration: none;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        margin: 0 0 43px;

        .eltdf-widget-title {
            margin: 0 0 30px;
        }

        form {
            margin-top: 20px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #000;
                height: 1px;
                position: relative;

                .ui-slider-handle {
                    background-color: $default-text-color;
                    border-radius: 50%;
                    cursor: pointer;
                    height: 11px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -5px;
                    width: 11px;
                    z-index: 2;

                    &:last-child {
                        @include eltdfTransform(translateX(-50%));
                    }
                }

                .ui-slider-range {
                    background-color: #000;
                    display: block;
                    height: 3px;
                    position: absolute;
                    top: -1px;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 12px 0 0;

            .button {
                float: right;
                line-height: 30px; // to match button height
                font-size: 15px;
                padding: 0 !important;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                letter-spacing: 0.01em;
                vertical-align: middle;
                @include eltdfAnimationLineVisible($color: currentColor);

                &:hover {
                    @include eltdfAnimationLineHidden($color: currentColor);
                    color: $default-text-color;
                    background-color: transparent;
                }
            }

            .price_label {
                float: left;
                font-family: $default-heading-font;
                font-weight: 500;
                line-height: 30px; // to match button height

                > span{
                    color: #2d2d2d;
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        margin: 0 0 50px;

        .eltdf-widget-title {
            margin: 0 0 32px;
        }

        ul {
            li {
                min-height: $thumb;
                position: relative;
                display: table;
                margin: 0 0 20px;
                font-size: 0;

                &:last-child{
                    margin: 0;
                }

                a {
                    img {
                        left: 0;
                        max-width: $thumb;
                        position: absolute;
                        top: $widget-list-item-padding;
                        width: 100%;
                        max-height: $thumb;
                        object-fit: cover;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }

                > span.quantity{

                    span{
                        float: right;
                        margin-left: 3px;
                    }
                }

                .star-rating{
                    margin-bottom: 5px;
                }

                .reviewer{
                    font-family: $default-text-font;
                    font-size: 10px;
                    text-transform: uppercase;
                    letter-spacing: .16em;
                    font-weight: 400;
                }

                > del{
                    margin-right: 9px;
                }

                > ins{
                    clear: initial;
                    margin: 0;
                }

                //> .amount{
                //    float: none;
                //    display: inline-block;
                //}
				//
                //> .amount:nth-child(4){
                //    margin: 0;
                //}
            }
        }
    }

    &.widget_recent_reviews{
        ul{
            li{
                a{
                    span{
                        padding: 0;
                    }

                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart,
    &.widget_recent_reviews {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include eltdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: -15px;
            top: 3px;
            text-decoration: none;
            padding: 0;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include eltdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include eltdfButtonDefaultStyle();
                @include eltdfButtonSize();
                @include eltdfAnimationLineVisible($bottom: 2px);
                padding: 0 !important;
                color: $default-text-color !important;
                background-color: transparent !important;
                border: 0;
                letter-spacing: 0.01em;
                vertical-align: middle;

                &:hover {
                    @include eltdfAnimationLineHidden($bottom: 2px);
                }

                &:first-child {
                    float: right;
                }

                &:last-child {
                    float: left;
                }
            }
        }
    }

    &.widget_product_tag_cloud {

    .eltdf-widget-title {
        margin: 0 0 26px;
    }

        .tagcloud {

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                font-family: inherit;
                background-color: transparent;
                font-size: 10px !important;
                text-transform: uppercase;
                font-weight: 500;
                letter-spacing: .160em;
                line-height: 18px;

                &:after {
                    content: ',';
                    color: $default-text-color;
                }

                &:last-child {

                    &:after {
                        display: none;
                    }
                }
            }
        }
    }

    &.widget_product_categories{
        margin: 0 0 46px;

        select{
            width: 100%;
        }

    }

    &.widget_layered_nav{

        .select2-container--default {
            height: auto;

            .select2-selection {
                height: auto;

                .select2-selection__rendered {

                    .select2-search--inline {
                        margin: 0;
                        width: 100%;

                        input {
                            border: 0;
                            margin: 0;
                            padding: 0;
                        }
                    }
                }
            }
        }
    }

    &.widget_product_search {
        margin: 0 0 54px;

        .woocommerce-product-search {
            @include eltdfRelativeHolderLayout();
            clear: both;

            > div {
                @include eltdfRelativeHolderLayout();
                border-bottom: 1px solid $default-border-color;
                box-sizing: border-box;
	            @include eltdfTransition(border-color 0.2s ease-in-out);

                &:focus-within {
                    border-bottom-color: $first-main-color;
                }
            }

            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                margin: 0;
                height: 40px;
                line-height: 32px;
                color: $default-text-color;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include eltdfTransition(color .2s ease-out);
            }

            input {
                width: calc(100% - 40px);
                font-family: inherit;
                font-size: 10px;
                letter-spacing: .160em;
                text-transform: uppercase;
	            @include eltdfTransition(border-color 0.2s ease-in-out);
                @include eltdfPlaceholder {
                    color: inherit;
                }
	
	            &:focus {
		            color: $default-heading-color;
		            border-color: $default-text-color;
	            }
            }

            button {
                width: 40px;
                padding: 0;
                font-size: 16px;
                cursor: pointer;
                text-align: right;

                &:hover {
                    color: $default-heading-color;
                }

                span {
                    display: block;
                    line-height: inherit;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */