/* ==========================================================================
   YITH Quick View styles - begin
   ========================================================================== */

#yith-quick-view-modal {
    z-index: 1050;

    .yith-quick-view-overlay {
        background: rgba( #f6f6f6, 0.7);
    }

    .yith-wcqv-wrapper {
        height: 680px;
        width: 1084px;
    }

    .yith-wcqv-main {
        overflow: -moz-scrollbars-none;
        -webkit-box-shadow: none;
        box-shadow: none;
    }

    .yith-wcqv-main::-webkit-scrollbar {
        display: none;
    }
    
    .eltdf-out-of-stock {
        right:50px
    }

    #yith-quick-view-content {

        > .product{
            margin-bottom: 0;

            > .product{
                margin-bottom: 0;
            }
        }

        .product {

            div.images {
                width: 50%;

                figure.woocommerce-product-gallery__wrapper {
                    margin: 0;

                    div:first-child {
                        padding-right: 10px;
                    }

                    div:not(:first-child) {
                        display: none;
                        width: calc(50% - 10px);
                        float: left;
                        padding-right: 10px;
                    }

                    a, img {
                        display: block;
                        height: 100%;
                        height: -webkit-fill-available;
                        object-fit: cover;
                    }
                }
            }
        }

        div.summary {
            width: calc(50% - 50px);
			padding: 54px 15px 15px 52px;

            @include phone-portrait {
                padding: 50px 0px 15px 21px;
            }

            .product_title.entry-title {
                font-size: 40px;
                line-height: 1.125em;
                margin-bottom: 7px;
            }

            .woocommerce-product-rating {
                position: relative;
                display: inline-block;
                width: 100%;
                vertical-align: middle;
                line-height: 1;
				margin: 13px 0 0px;
                font-family: $default-heading-font;
                font-size: 14px;

                a.woocommerce-review-link {
                    display: inline-block;
                    color: $default-text-color;

                    &:hover {
                        color: $default-heading-color;
                    }
                }
            }

            .yith-wcwl-add-to-wishlist {
                .yith-wcwl-add-button a,
                .yith-wcwl-wishlistaddedbrowse a,
                .yith-wcwl-wishlistexistsbrowse a{
                    font-size: 14px !important;
                    text-transform: capitalize;

                    &:before {
                        top: 1px;
                    }
                }
            }
            .woocommerce-product-details__short-description {
                margin: 47px 0 13px;
            }

            form.cart {
                margin: 43px 0 0;
            }

            .eltdf-quantity-buttons {
                margin-right: 11px;
            }

            .product_meta {
                display: none;
                margin: 42px 0 0;
            }
        }
    }
}

#yith-quick-view-modal #yith-quick-view-close,
.yith-quick-view.yith-modal #yith-quick-view-close {
    top: 24px;
	right: 24px;
	color: #000;
	background: transparent url("../../assets/img/close-icon.svg") no-repeat;
	width:16px;
	height: 17px;
    font-size: 0;
    line-height: 15px;
    font-weight: 400;
    text-align: center;
    opacity: 1;
    border: 0;
}

/*hides default button*/
ul.products>.product .button.yith-wcqv-button {
    display: none !important;
}

ul.products>.product .eltdf-pl-yith {
    display: inline-block;
    position: absolute;
    bottom: 0;
    left: 50%;
    @include eltdfTransform(translateX(-50%));
    z-index: 100;
    overflow: hidden;
    font-size: 0;
    width: 121px;

    .eltdf-yith-wcqv-holder {
        display: inline-block;
        height: 29px;
        vertical-align: middle;

        a {
            display: block;
            color: #fff;
            font-family: $default-heading-font;
            font-size: 13px;
            font-weight: 700;
            text-transform: uppercase;

            span {
                display: inline-block;
                background-color: #000;
                line-height: 1;
                padding: 8px 10px;
            }

            span.eltdf-yith-wcqv-icon {
                background-color: #404040;
            }
        }
    }

}

/* ==========================================================================
   YITH Quick View styles - end
   ========================================================================== */