/* ==========================================================================
   Product Single shortcode styles - begin
   ========================================================================== */

.eltdf-product-single {
    position: relative;
    display: inline-block;
    vertical-align: middle;

    .touch & {
        cursor: pointer;
    }

    .eltdf-product-single-content {
        @include eltdfTableLayout();
    }

    .eltdf-product-single-text {
        @include eltdfTableCellLayout();
        width: 50%;
        padding: 0 14%;
        box-sizing: border-box;
        background-color: #fff;
    }

    .eltdf-product-single-text-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
    }

    .eltdf-single-thumbnail-holder {
        position: relative;
        display: inline-block;
        width: 100%;
        vertical-align: middle;
        overflow: hidden;

        img {
            display: block;
            width: 100%;
        }
    }

    .eltdf-product-single-link {
        @include eltdfAbsoluteHolderLayout();
        z-index: 1;
    }

    .eltdf-sp-text-wrapper {
        position: absolute;
        display: inline-block;
        width: 100%;
        left: 0;
        bottom: 27px;
        vertical-align: middle;
        margin: 20px 0 0;
    }

    .eltdf-product-single-title {
        display: inline-block;
        width: auto;
        margin: 0 0 7px;
    }

    .eltdf-sp-price {
        position: relative;
        display: block;
        @include eltdfWooCommercePriceStyle();
        color: $default-text-color;
        clear: both;
        margin: 0;
        font-weight: 300;
        font-size: 14px;
    }

    .eltdf-sp-button {
        margin-top: 20px;
        position: relative;
        z-index: 2;
    }

    .eltdf-product-single-image {
        position: relative;
        @include eltdfTableCellLayout();
        width: 50%;

        img {
            display: block;
        }
    }

    &.eltdf-product-text-outside.eltdf-product-single-featured-left {
        .eltdf-sp-text-wrapper {
            left: 80%;
            right: auto;
            text-align: left;
        }
    }

    &.eltdf-product-text-outside.eltdf-product-single-featured-right {
        .eltdf-sp-text-wrapper {
            right: 80%;
            left: auto;
            text-align: right;
        }
    }
}

.rev_slider_wrapper .eltdf-product-single .eltdf-product-single-image img,
.rev_slider_wrapper .eltdf-product-single .eltdf-single-thumbnail-holder img {
    width: 100% !important;
    height: auto !important;
}

/* ==========================================================================
   Product single shortcode styles - end
   ========================================================================== */