/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    font-family: $default-text-font;
    font-weight: 400;
    font-size: 11px;
    text-transform: uppercase;
    position: absolute;
    display: block;
    top: 15px;
    width: 50px;
    height: 20px;
    margin: 0;
    padding: 0;
    color: #fff;
    line-height: 20px;
    letter-spacing: 1px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include eltdfTransform(rotate(90deg) translateZ(0));
}

@mixin eltdfWooCommerceSaleStyle() {
    left: 1px;
    background-color: #000;
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    right: 1px;
    background-color: #000;
}

@mixin eltdfWooCommerceNewProductStyle() {
    left: 1px;
    background-color: #fff;
    color: #000;

}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    font-family: $default-heading-font;
    font-weight: 400;
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: inherit;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        color: #a5a5a5;
        margin-right: 8px;
    }
    
    ins {
        text-decoration: none;
    }
}

@mixin eltdfWooCommercePriceStyleSingle() {
    font-family: $default-heading-font;
    font-weight: 600;
    font-size: 18px;
    position: relative;
    display: block;
    margin: 14px 0 0;
    line-height: 1;
    color: $default-heading-color;

    * {
        font-size: inherit;
        line-height: inherit;
    }

    del {
        font-weight: 500;
        font-size: 15px;
        margin-right: 8px;
    }

    ins {
        text-decoration: none;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "Ionicons";
    font-size: 16px;
    line-height: 1;
    letter-spacing: 2px;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        color: #a8a8a8;
        content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        color: #a8a8a8;
        content: "\f4b3";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */