/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {

    .eltdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: top;

        .eltdf-quantity-input,
        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid #d3d3d3;
            text-align: center;
            box-sizing: border-box;
        }

        .eltdf-quantity-input {
            position: relative;
            height: 50px;
            width: 50px;
            padding: 0;
            margin: 0 25px 0 0;
            font-family: $default-heading-font;
            line-height: 48px;
            font-size: 13px;
            font-weight: 600;
            color: #2d2d2d;
            outline: 0;
        }

        .eltdf-quantity-minus,
        .eltdf-quantity-plus {
            position: absolute;
            right: 0;
            width: 25px;
            height: 25px;
            line-height: 23px;
            border-left: 0;
            cursor: pointer;
            color: #464646;
            @include eltdfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .eltdf-quantity-plus {
            top: 0;
            border-bottom: 0;
        }

        .eltdf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .eltdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @extend %input-style;
    }

    .coupon {

        input[type="text"] {
            width: 200px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */