/* ==========================================================================
   WooCommerce Checkout page styles - begin
   ========================================================================== */

.eltdf-woocommerce-page {
    
    .checkout_coupon {
        display: block;
        margin: 0 0 34px;
        
        p {
            margin: 0;
        }
    }
    
    &.woocommerce-checkout {

        form.login {
            display: block;
            margin: 0 0 34px;

            > *:first-child {
                margin: 0 0 15px;
            }

            p {
                margin: 0;
            }

            input[type='checkbox'] {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                top: -1px;
                margin: 0 3px 0 5px;
            }

            .lost_password {
                display: block;
                margin: 15px 0 0;
            }
        }

        .select2-container--default .select2-selection--single .select2-selection__rendered {
            text-transform: lowercase;
            font-size: 14px;
            letter-spacing: 0;
        }
    }
    
    .woocommerce-checkout .col2-set:before,
    .woocommerce-checkout .col2-set:after {
        content: '';
        display: table;
    }
    
    .woocommerce-checkout .col2-set:after {
        clear: both;
    }
    
    .woocommerce-checkout {

        strong{
            font-weight: 300;
        }

        abbr.required {
            border: none !important;
            text-decoration: none !important;
        }

        .select2-container--default {
            text-align: left !important;
            border-bottom: 1px solid $default-border-color !important;

            .select2-selection--single .select2-selection__arrow {

                right: 0;

                &:before {
                    font-family: "linea-arrows-10";
                    font-size: 13px;
                    content: "\e019";
                }
            }
        }
        
        .col-1,
        .col-2 {
            position: relative;
            display: inline-block;
            width: 49%;
            margin-right: 1%;
            float: left;
            vertical-align: middle;

            input[type=text],
            input[type=email],
            input[type=tel],
            input[type=password] {
                margin-bottom: 39px;
                color: $default-heading-color;
                font-size: 10px;
                font-weight: 400;
                text-transform: uppercase;
                letter-spacing: .16em;

            }
            
            h3 {
                margin: 8px 0 25px;
            }
            
            p {
                margin: 0;
            }
            
            label:not(.checkbox) {
                display: block;
                margin: 5px 0;
                font-family: $default-heading-font;
                font-weight: 600;
            }
            
            .form-row {
                width: 49%;
                margin-right: 2%;
                float: left;
            }
            
            .form-row-last {
                margin-right: 0;
            }
            
            .form-row-wide,
            .form-row.notes {
                width: 100%;
            }
        }
        
        .col-2 {
            margin-left: 1%;
            margin-right: 0;
        }
        
        .create-account {
            margin: 0 0 15px;
            
            input, label {
                display: inline-block;
                vertical-align: middle;
            }
        }
        
        h3#order_review_heading {
            margin: 18px 0;
        }
        
        table {
            padding: 0;
            margin: 0 0 35px;
            
            tr {
                border: 0;
                border-bottom: 1px solid $default-border-color;
                
                &.shipping {
                    
                    #shipping_method {
                        list-style: none;
                        margin: 0;
                        
                        li {
                            margin: 3px 0;
                            padding: 0;
                            
                            > * {
                                display: inline-block;
                                vertical-align: middle;
                                margin: 0;
                            }

                            > label {
                                padding-left: 6px;
                            }
                        }
                    }
                }
            }
            
            td, th {
                padding: 14px 0;
                text-align: left;
                vertical-align: middle;
            }
            
            th {
                border: 0;
            }
            
            .variation {
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    padding: 0;
                    margin: 0;
                }
                
                p {
                    margin: 0;
                }
            }
        }
        
        .select2-container {
            border-radius: 0;
        }
        
        input[type='radio'],
        input[type='checkbox'] {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            top: -1px;
            margin: 0 3px 0 5px;
        }
        
        .woocommerce-checkout-payment {
            display: block;
            margin: 20px 0 70px;
            
            ul {
                list-style: none;
                margin: 0 0 35px;
                padding: 0;
                
                li {
                    padding: 14px;
                    border-bottom: 1px solid $default-border-color;

                    &.woocommerce-info {
                        margin-left: 0;
                        margin-right: 0;
                    }

                    &.wc_payment_method.payment_method_cod {
                        padding-left: 0;
                        padding-right: 0;
                    }
                    
                    input, label {
                        display: inline-block;
                        vertical-align: middle;
                    }
                    
                    &.payment_method_paypal {
                        
                        label {
                            
                            img {
                                vertical-align: middle;
                                margin: 0 8px;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Checkout page styles - end
   ========================================================================== */