.eltdf-pl-holder {
    .eltdf-pl-categories {
        position: relative;
        padding-bottom: 35px;
		float: left;
        text-align: center;
        width: 100%;

        h6.eltdf-pl-categories-label {
            margin: 0;
            display: none;
            font-size: 13px;

            &:after {
                content: "\35";
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: "ElegantIcons";
                font-size: 14px;
                line-height: 1em;
                margin-left: 8px;
                top: -1px;
            }
        }

        ul {
            list-style: none;
            display: inline-block;
            margin: 0;

            li {
                display: inline-block;
                float: left;
                position: relative;
                padding: 0 20px;

                @include phone-portrait {
                    display: block;
                    float: none;
                    padding: 0;
                }

                &:first-child {
                    padding-left: 0;
                }

                a {
                    position: relative;
                    color: $default-heading-color;
                    font-family: $default-heading-font;
                    font-weight: 600;
                    font-size: 14px;
                    @include eltdfAnimationLineHidden($bottom: 0px, $color: currentColor);

                    &:hover,
                    &.active {
                        @include eltdfAnimationLineVisible($bottom: 0px, $color: currentColor);
                    }

                }
            }
        }
    }
}