/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
    display: none;
    opacity: 0;
 
    //vanishing effect
    &.eltdf-wishlist-vanish-out {
        display: none;
        opacity: 0;
    }
}

.eltdf-yith-wcqv-holder {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 8px 0 0 0;
    text-align: center;
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        position: relative;
        display: inline-block;
        vertical-align: top;
        font-size: 0 !important;
        line-height: inherit;
        color: #000;

        &:before {
            content: "\e030";
            position: relative;
            display: inline-block;
            font-family: "ElegantIcons";
            font-size: 11px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            @include eltdfTransition(opacity .2s);
            padding-top: 2px;
        }

        // content: "\f00c";

        &:not(.eltdf-adding-to-wishlist):hover {
            &:before {
                opacity: 1;
            }
        }

        &.eltdf-adding-to-wishlist {
            @include keyframes(eltdfWishlistLoading) {
                0%, 100% {
                    opacity:1;
                }

                50% {
                    opacity: 0.2;
                }
            }

            &:before {
                @include eltdfAnimation(eltdfWishlistLoading 1s ease infinite);
            }
        }

        &:hover {
            color: #000;
        }
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        &:before {
            content: "\e089";
        }
    }
}

.yith-wcwl-add-button {
    .ajax-loading {
        display: none !important;
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    span {
        display: none;
    }

    a {

        &:before {
            color: #000;
        }
    }
}

.woocommerce-wishlist {

    .wishlist-title{
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 15px 30px;
        margin: 50px 0 30px;
        line-height: 48px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 50px 0 100px;

        thead {
            tr {
                border: none;
                color: $default-heading-color;
                font-family: $default-heading-font;
                font-size: 18px;
                font-weight: 700;
                line-height: 1.235em;
                text-transform: capitalize;

                th {
                    border: none;
                    text-align: left;
                    padding: 16px 12px;
                    font-weight: 600;

                    &.product-name {
                        position: absolute;
                        left: 0;
                        padding-left: 0;
                    }

                    &.product-remove {
                        width: 18px;
                    }
                }
            }
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 1px solid #e6e6e6;

                td {
                    vertical-align: middle;
                    text-align: left;
                    padding: 30px 12px;

                    &.product-remove {
                        text-align: left;

                        a {
                            color: $default-text-color;
                            font-size: 0;

                            &:before {
                                content: "\4d";
                                display: inline-block;
                                vertical-align: middle;
                                speak: none;
                                color: #000;
                                font-family: "ElegantIcons";
                                font-size: 24px;
                                line-height: inherit;
                                font-style: normal;
                                font-weight: 400;
                                font-variant: normal;
                                text-transform: none;
                                text-rendering: auto;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                            &:hover {
                                color: $default-heading-color;
                            }
                        }
                    }

                    &.product-thumbnail {
                        padding-left: 8px;
                        padding-right: 8px;
                        width: 115px;

                        a, img {
                            display: block;
                            max-width: 115px;
                            max-height: 115px;
                            object-fit: cover;
                        }
                    }

                    &.product-name {


                        a {
                            color: $default-heading-color;
                            font-family: $default-heading-font;
                            font-size: 15px;
                            font-weight: 500;
                            line-height: 1.235em;
                            text-transform: capitalize;
                            text-align: left;
                        }

                        .yith-wcqv-button {
                            display: none;
                        }
                    }

                    &.product-price{
                        font-size: 14px;

                        ins{
                            text-decoration: none;
                        }
                    }

                    &.product-stock-status {
                        font-size: 14px;

                        span {
                            color: $default-text-color !important;
                        }
                    }

                    &.product-add-to-cart {
                        text-align: right;

                        a {
                            border-radius: 0;
                            border: 0;
                            display: inline-block !important;

                            &:after{
                                content:'';
                                display: none;
                            }
                        }
                    }
                }
            }
        }

        .yith-wcwl-share{
            margin: 0;
        }
    }
}

.eltdf-single-product-summary, #yith-quick-view-content div.summary {
    .yith-wcwl-add-to-wishlist {
        width: auto;
        background-color: transparent;
        margin: 18px 0 0;

        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {
            a {
                font-size: 14px;
                line-height: inherit;
                color: $default-text-color;
                font-weight: 400;
                text-transform: lowercase;
                vertical-align: middle;

                &:hover {
                    color: $default-heading-color;
                }


                &:before {
                    content: "\e089";
                    font-size: 17px;
                    padding: 0 2px;
                }
            }
        }

        .yith-wcwl-add-button {
            a {
                &:before {
                    content: "\e030";
                    color: $default-heading-color;
                }
            }
        }
    }
}

.eltdf-wishlist-widget-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
    margin: 0 10px;
    font-size: 17px;

    &:hover {

        .eltdf-wishlist-opener-wrapper {

            .eltdf-wishlist-text{
                @include eltdfAnimationLineVisible($bottom:0px, $color: currentColor);
            }
        }
    }

    .eltdf-wishlist-opener-wrapper {
        display: block;

        .eltdf-wishlist-text{
            font-size: 14px;
            line-height: 23px;
            box-sizing: border-box;
            font-weight: 600;
            margin-left: 5px;
            @include eltdfAnimationLineHidden($bottom:0px, $color: currentColor);
        }

        > * {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            line-height: inherit;

            &:before {
                display: block;
                line-height: inherit;
            }
        }
    }

    &.eltdf-search-opener-svg-path {

        .eltdf-search-opener-wrapper {

            >* {
                vertical-align: middle;
            }

            > *,
            path {
                fill: currentColor;
                stroke: currentColor;
            }
        }
    }
}

.eltdf-light-header {
    .eltdf-wishlist-widget-holder {
        color: $header-light-color;
        .eltdf-wishlist-items-number {
            color: $header-dark-color;
        }
    }
}

.eltdf-dark-header {
    .eltdf-wishlist-widget-holder {
        color: $header-dark-color;
        .eltdf-wishlist-items-number {
            color: $header-dark-color;
        }
    }
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */