/* Woocommerce Product List Animated Shortcode - begin
========================================================================== */

.eltdf-pla-holder {
    @include eltdfRelativeHolderLayout();
    opacity:0;

    .touch & {
        opacity: 1;
    }

    .eltdf-pla-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        float: left;
        width: 100%;
        padding: 0;
        z-index: 100;
        text-align: center;
        box-sizing: border-box;
        @include eltdfTransform(translate3d(0,200px,0));
        will-change: transform;

        .no-touch & {
            &.eltdf-pla-animated {
                @include eltdfTransform(translate3d(0,0,0));
                -webkit-transition: -webkit-transform 2s cubic-bezier(0,0,0,1), opacity .6s;
                transition: transform 2s cubic-bezier(0,0,0,1), opacity .6s;
            }
        }

        .touch & {
            cursor: pointer;
            @include eltdfTransform(translate3d(0,0,0));
            opacity:1;
        }
	
	    .eltdf-pla-link {
		    @include eltdfAbsoluteHolderLayout();
	    }
    }


    &.eltdf-pla-animation-disabled{
        opacity: 1;
        
        .eltdf-pla-item{
            @include eltdfTransform(translate3d(0, 0, 0));
        }
    }

    $animated_list_columns: ('two', 'three', 'four', 'five', 'six');

    @for $i from 0 to length($animated_list_columns) { 
        &.eltdf-#{nth($animated_list_columns,$i+1)}-columns {
            $column_width: 100%/($i+2);

            .eltdf-pla-item {
                width: $column_width;

                @media only screen and (min-width: $ipad-landscape-plus-pixel) {

                    &:nth-child(#{$i+2}n+1) {
                        clear: both;
                    }
                }
            }
        }
    }
    
    .eltdf-pla-item {
        padding: 0;
        z-index: 100;
	
	    .eltdf-pla-inner {
		    position: relative;
		    overflow: hidden;
		    
		    .eltdf-pla-image {
			    img {
				    @include eltdfTransform(scale(1.06));
				    @include eltdfTransitionTransform(.45s cubic-bezier(.33,.02,0,.93));
			    }
		    }
		
		    .eltdf-pla-text {
			    @include eltdfAbsoluteHolderLayout();
			    padding: 0;
			    background-color: rgba(255, 255, 255, 0.9);
			    opacity: 0;
			    @include eltdfTransition(opacity .4s cubic-bezier(0.76, 0.14, 0, 0.74));
			
			    .eltdf-pla-icons-holder {
				    position: absolute;
				    display: inline-flex;
				    width: 120px;
				    top: 50%;
				    left: 50%;
				    background-color: #fff;
				    border: 1px solid;
				    opacity: 0;
				    z-index: 111;
				    @include eltdfFlexContainer(null, null, row-reverse);
				    @include eltdfTransform(translate(-50%,-50%));
				    @include eltdfTransition(opacity 0.2s ease-out);
				    @include eltdfFlickerFix();
			    
		            > .eltdf-pla-add-to-cart a.button,
		            > .yith-quickview-button,
		            > .yith-wcwl-add-to-wishlist,
		            .added_to_cart {
		                height: 43px;
		                width: 18px;
		                z-index: 300;
		                overflow: hidden;
		                padding: 0 16px;
		                font-size: 0;
		                vertical-align: bottom;
		                margin: 0;
		                border: 0;
		                box-sizing: initial;
			            @include eltdfTransition(opacity .2s ease-out);
		                
		                &:before, &:after {
		                    content: initial;
			                color:inherit;
		                }
			
			            &:hover {
				            opacity: .8;
			            }
		            }
		            
		            > .eltdf-pla-add-to-cart {
		                a.button {
		                    bottom: 0;
		                    right: -1px;
		                    
		                    &.added {
		                        display: none;
		                    }
		                    
		                    &.product_type_variable,
		                    &.product_type_external,
		                    &.add_to_cart_button {
			                    box-sizing: content-box;
			                    &:after {
				                    font-family: 'Ionicons';
				                    content: "\f110";
				                    font-size: 19px;
				                    line-height: 42px;
				                    font-weight: 500;
			                    }
		                    }
		                }
		                
		                .added_to_cart {
			                color: unset;
			                background-color: unset;
			                font-weight: unset;
			                
			                &:before {
				                content: initial;
			                }
			                
			                &:after {
				                content: "Z";
				                font-size: 18px;
				                line-height: 46px;
				                font-family: 'ElegantIcons';
			                }
		                }
		            }
		            
		            > .yith-quickview-button {
		                bottom: 0;
		                
		                &:before {
		                    content: "\f06e";
		                    font-size: 17px;
		                    line-height: 43px;
		                    font-family: 'FontAwesome';
		                }
		            }
		            
		            > .yith-wcwl-add-to-wishlist {
		                bottom: 0;
		                
		                > div {
		                    height: 100%;
		                    
		                    a {
		                        height: 100%;
		                        
		                        &:before {
		                            font-size: 16px;
		                            line-height: 45px;
		                            padding: 0;
		                            margin: 0;
		                        }
		                    }
		                }
		            }
			    }
		    }
        }
        
        &:hover {
            .eltdf-pla-image {
                img {
	                @include eltdfFlickerFix();
                    @include eltdfTransform(translateX(-3%) scale(1.06));
	                @include eltdfTransitionTransform(1s cubic-bezier(.33,.02,0,.93))
                }
            }
	
	        .eltdf-pla-inner .eltdf-pla-text {
		        opacity: 1;
		        
		        .eltdf-pla-icons-holder {
			        opacity: 1;
		        }
	        }
        }
        
        .eltdf-pla-title {
            margin: 0;
        }
        
        .eltdf-pla-category {
            font-size: 10px;
            text-transform: uppercase;
            font-weight: 500;
            letter-spacing: .160em;
            line-height: 21px;
            margin: 0;
        }
        
        .eltdf-pla-price {
            @include eltdfWooCommercePriceStyle();
	        font-size: 14px;
	        margin: 0;
	        line-height: 21px;
        }
        
        .eltdf-pla-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 0;
        }
        
        .eltdf-pla-rating {
            @include eltdfWooCommerceRatingsStyle();
        }
        
        .eltdf-pla-excerpt {
            color: $default-heading-color;
            line-height: 1.3em;
            margin: 11px 0 0;
            display: inline-block;
            font-size: 14px;
        }
    }
	
	&:not(.eltdf-product-add-to-cart-in-box):not(.eltdf-info-below-image) {
		.eltdf-pla-add-to-cart {
			margin: 0;
		}
		
		.button,
		.added_to_cart {
			@include eltdfButtonDefaultStyle();
			@include eltdfButtonSize();
			
			padding: 0 !important;
			color: $default-text-color;
			background-color: transparent;
			border: 0;
			letter-spacing: 0.01em;
			vertical-align: middle;
			
			> span {
				@include eltdfAnimationLineVisible();
			}
			
			&:hover {
				background-color: transparent;
				color: $default-text-color;
				
				> span {
					@include eltdfAnimationLineHidden();
				}
			}
			
			.eltdf-btn-text {
				display: inline-block;
				vertical-align: middle;
			}
			
			white-space: nowrap;
			z-index: 3;
		}
		
		.button.added {
			display: none;
		}
	}
	
	&.eltdf-hover-type-standard-dark .eltdf-pla-icons-holder {
		background: #000 !important;
		
		.yith-quickview-button,
		.yith-wcwl-add-button,
		.yith-wcwl-wishlistaddedbrowse a:before,
		.yith-wcwl-wishlistexistsbrowse a:before,
		.yith-wcwl-add-to-wishlist *,
		.eltdf-pla-add-to-cart a.button,
		.added_to_cart {
			color:#fff !important;
		}
		.add_to_cart_button {
			box-sizing: content-box;
		}
	}

    .eltdf-pla-text-wrapper {
	    @include eltdfRelativeHolderLayout();
	    margin: 23px 0 38px;
    }
}

/* Woocommerce Product List Animated Shortcode - end
========================================================================== */