/* ==========================================================================
   WooCommerce Track Order page styles - begin
   ========================================================================== */

.eltdf-woocommerce-page.eltdf-woocommerce-order-tracking {

    .eltdf-default-page-template {
        height: 100vh;
    }

    &.eltdf-header-top-enabled {
        .eltdf-default-page-template {
            height: calc(100vh - 46px);
        }
    }
    
    .woocommerce {
        margin: 0 0 45px;
        
        > .track_order {
            display: block;
            margin: 0 0 60px;
            
            > p:not(.form-row) {
                display: block;
                margin: 0 0 28px;
                font-size: 16px;
                color: #000;
            }
            
            .form-row-first,
            .form-row-last {
                @include eltdfRelativeHolderLayout();
                margin: 0;
                
                label {
                    display: block;
                    margin: 14px 0;
                    color: $default-heading-color;
                    font-size: 18px;
                    line-height: 38px;
                    font-weight: 600;
                    font-family: $default-heading-font;
                }

                .input-text{
                    color: #000;
                    color: #000;
                    font-family: Montserrat,sans-serif;
                    font-size: 10px;
                    font-weight: 400;
                    letter-spacing: .16em;

                }
            }
            
            .button {
                margin: 13px 0 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Track Order page styles - end
   ========================================================================== */