/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - begin
   ========================================================================== */

.woocommerce {
    
    .eltdf-onsale,
    .eltdf-out-of-stock,
    .eltdf-new-product {
        @include eltdfWooCommerceMarksGeneralStyle();
    }
    
    .eltdf-onsale {
        @include eltdfWooCommerceSaleStyle();
    }
    
    .eltdf-out-of-stock {
        @include eltdfWooCommerceOutOfStockStyle();
    }

    .eltdf-new-product {
        @include eltdfWooCommerceNewProductStyle();
    }

    .sale .eltdf-new-product {
        left: 21px;
    }
}

.eltdf-woo-single-page {
    
    .eltdf-single-product-content {
        
        .eltdf-onsale {
            left: 10px;
        }

        .eltdf-new-product {
            left: 10px;
        }

        .sale .eltdf-new-product {
            left: 30px;
        }
        
        .eltdf-out-of-stock{
            right: 10px;
        }
    }
    
    &.eltdf-woo-single-thumb-on-left-side {
        
        .eltdf-single-product-content {
            
            .eltdf-onsale {
                left: 122px;
            }

            .eltdf-new-product {
                left: 122px;
            }

            .sale .eltdf-new-product {
                left: 142px;;
            }
            
            .eltdf-out-of-stock{
                right: 0;
            }
        }

        .sale .eltdf-single-product-content{
            .eltdf-new-product {
                left: 142px;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock styles - end
   ========================================================================== */