/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page .entry-summary{
	.yith-wcwl-add-button a, .yith-wcwl-wishlistaddedbrowse a, .yith-wcwl-wishlistexistsbrowse a{
		font-size: 14px !important;
		font-weight: 500 !important;
		font-family: $default-heading-font;
	}
}

.eltdf-woo-single-page, #yith-quick-view-modal{
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
						overflow: hidden;
					}
					
					$thumb_size: 111;
					$space_between_items: 10;
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							$first_thumb_offset: $thumb_size + $space_between_items;
							
							left: #{$first_thumb_offset}px;
							width: calc(100% - #{$first_thumb_offset}px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: #{$thumb_size}px;
								padding: 0 0 #{$space_between_items}px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
						}
					}
					
					$thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4', 'columns-5');
					@for $i from 0 to length($thumb_3_columns_label) {
						&.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
							$column_value: $i+2;
							$column_size: 100 / $column_value;
							$column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);
							
							.woocommerce-product-gallery__image {
								
								&:not(:first-child) {
									height: $column_width;
								}
								
								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
									
									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: ($column_size * $i)#{'%'};
										$second_param: ($space_between_items / $column_value * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});
										
										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}
	}

	&.eltdf-woo-single-thumb-full-width-gallery {

		.woocommerce-tabs{
			ul.tabs{
				> li{
					a{
						padding: 13px 16px;
					}
				}
			}
		}

		.eltdf-content .eltdf-content-inner > .eltdf-container > .eltdf-container-inner {
			width: 100%;
			padding-top: 0;

			.eltdf-single-product-content {
				margin: 0 0 -5px;

				.eltdf-single-product-summary {
					width: 34%;
					padding: 54px 65px 0 35px;

                    @include laptop-landscape-mac{
                        width: 47%;
                    }
				}

				@media screen and (max-width: 1200px){

					.eltdf-single-product-summary{
						width: 100% !important;
						padding: 0 30px !important;
					}

					.images  {
						width: 100% !important;
						padding: 0 10px 0 0 !important;
					}
				}

				@media screen and (max-width: 680px){

					.eltdf-single-product-summary{
						padding: 0 30px !important;
					}
				}

				.images {
					width: 66%;

                    @include laptop-landscape-mac{
                        width: 53%;
                    }

					@media only screen and (min-width: $smaller-phone-portrait) {

						figure {
							width: 100%;
							margin: 0;
						}

						.woocommerce-product-gallery__image {

							&:first-child {
								float: left;
								left: 0;
								width: 50%;
								padding: 0 0 10px 10px;
							}
						}

						&.woocommerce-product-gallery--with-images {

							.woocommerce-product-gallery__image {

								&:not(:first-child) {
									position: relative;
									width: 50%;
									height: auto;
									float: left;
									padding: 0 0 10px 10px;

									a {
										height: 100%;

										img {
											height: 100%;
										}
									}
								}

								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);

									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: (25 * $i)#{'%'};
										$second_param: (5 * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});

										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}

		.related.products,
		.upsells.products {
			padding: 0 10px;
			box-sizing: border-box;
		}
	}

	&.eltdf-woo-single-thumb-sticky-info {

		&.eltdf-woo-single-info-inside-summary {

			.eltdf-single-product-content {
				margin: 0 0 13px;
			}
		}

		.related.products>h2,
		.upsells.products>h2 {
			margin: 50px 0 32px;
		}

		.eltdf-content .eltdf-content-inner > .eltdf-container > .eltdf-container-inner {
			width: 100%;
			padding-top: 0;

			.eltdf-single-product-content {

				@media screen and (max-width: 1200px){

					.eltdf-single-product-summary{
						width: 100% !important;
						padding: 0 10px !important;
					}

					.images  {
						width: 100% !important;
						padding: 0 10px 0 0 !important;
					}
				}

				.eltdf-single-product-summary {
					width: 50%;
					padding: 0 0 0 30px;
					max-width: 610px;

					.entry-summary {
						padding-top: 43px;
						box-sizing: border-box;
					}

					@media only screen and (max-width: $laptop-landscape-large) {
						.entry-summary {
							padding-right: 100px;
						}
					}

					@media only screen and (max-width: $laptop-landscape-medium) {
						.entry-summary {
							padding-right: 80px;
						}
					}

					@media only screen and (max-width: $ipad-landscape) {
						.entry-summary {
							padding-right: 0;
						}
					}

					@media only screen and (min-width: $ipad-landscape-plus-pixel) {
						.woocommerce-tabs .entry-content {
							max-height: 200px;
							overflow: hidden;
							overflow-y: auto;
						}
					}
				}

				.images {
					width: 50%;

					@media only screen and (min-width: $ipad-portrait) {

						figure {
							width: 100%;
							margin: 0;
						}

						.woocommerce-product-gallery__image {

							&:first-child {
								float: none;
								left: 0;
								width: 100%;
								padding: 0 10px;
							}
						}

						&.woocommerce-product-gallery--with-images {

							.woocommerce-product-gallery__image {

								&:not(:first-child) {
									position: relative;
									width: 100%;
									height: auto;
									float: none;
									padding: 10px 10px 0 10px;

									a {
										height: 100%;

										img {
											height: 100%;
										}
									}
								}

								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);

									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: (25 * $i)#{'%'};
										$second_param: (5 * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});

										top: $topPos;
									}
								}
							}
						}
					}

					@media screen and (max-width: $ipad-landscape){
						.woocommerce-product-gallery__image{
							width: 100% !important;
							padding-right: 0 !important;
						}
					}
				}
			}
		}

		.related.products,
		.woocommerce-tabs,
		.upsells.products {
			padding: 0 10px;
			box-sizing: border-box;
		}
	}

	&.eltdf-woo-single-thumb-large-images {

		.related.products>h2,
		.upsells.products>h2 {
			margin: 50px 0 32px;
		}

		.woocommerce-tabs{
			ul.tabs{
				> li{
					a{
						padding: 13px 16px;
					}
				}
			}
		}

		.eltdf-content .eltdf-content-inner > .eltdf-container > .eltdf-container-inner {
			padding-top: 80px;

			.eltdf-single-product-content {
				margin: 0 0 20px;

				.eltdf-single-product-summary {
					width: 46%;
					padding: 4px 0 0 35px;
				}

				@media screen and (max-width: 1200px){

					.eltdf-single-product-summary, .images  {
						width: 100% !important;
						padding: 0 !important;
					}
				}

				.images {
					width: 54%;

					@media only screen and (min-width: $smaller-phone-portrait) {

						figure {
							width: 100%;
							margin: 0;
						}

						.woocommerce-product-gallery__image {

							&:first-child {
								float: none;
								left: 0;
								width: 100%;
								padding: 0;
							}
						}

						&.woocommerce-product-gallery--with-images {

							.woocommerce-product-gallery__image {

								&:not(:first-child) {
									position: relative;
									width: 100%;
									height: auto;
									float: none;
									padding: 10px 0 0 0;

									a {
										height: 100%;

										img {
											height: 100%;
										}
									}
								}

								$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
								@for $i from 0 to length($thumbnail_numbers) {
									$thumbnail_number_value: nth($thumbnail_numbers, $i+1);

									&:nth-child(#{$thumbnail_number_value}#{n}) {
										$first_param: (25 * $i)#{'%'};
										$second_param: (5 * $i)#{px};
										$topPos: calc(#{$first_param} + #{$second_param});

										top: $topPos;
									}
								}
							}
						}
					}
				}
			}
		}

		&.eltdf-woo-single-info-inside-summary {

			.eltdf-content .eltdf-content-inner > .eltdf-container > .eltdf-container-inner {

				.eltdf-single-product-content {

					margin: 0 0 14px;
				}
			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 62%;
		    margin: 0;
		    padding: 0 30px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include eltdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4', 'columns-5');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltdf-single-product-summary, #yith-quick-view-content div.summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 38%;
        padding: 0 0 0 21px;
        float: left;
        box-sizing: border-box;
	
	    .eltdf-single-product-title {
		    display: block;
		    margin: 4px 0 16px;
	    }
	
	    .price {
		    @include eltdfWooCommercePriceStyleSingle();
	    }
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    //line-height: 1;
		    margin: 16px 0 3px;

			.star-rating{
				font-size: 22px;
				letter-spacing: 0.15em;
			}

			.woocommerce-review-link{
				font-size: 16px;
				vertical-align: text-top;
				margin-left: 4px;
				line-height: 24px;
				font-family: $default-text-font;
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdfRelativeHolderLayout();
		    margin: 47px 0 0;
		
		    p {
				font-size: 16px;
				line-height: 26px;
			    margin: 0;
				color: #2d2d2d;
		    }
	    }
	
	    .product_meta {
		    margin: 52px 0 0;
			font-size: 10px;
			text-transform: uppercase;
            font-weight: 500;
            letter-spacing: .29em;
            color: #3d3d3d;
		
		    > span {
			    display: block;
			    margin: 3px 0;
			
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .eltdf-woo-social-share-holder {
		    @include eltdfRelativeHolderLayout();
		    margin: 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    margin: 18px 0 0;
			font-size: 14px;
			line-height: 1.8em;
			font-weight: 600;
			font-family: $default-heading-font;
	    }
	
	    form.cart {
		    @include eltdfRelativeHolderLayout();
		    margin: 57px 0 0;
	    }
	
	    .eltdf-quantity-buttons {
		    margin: 0 15px 0 0;
	    }

		.grouped_form {

			button.button {
				width: 100%;
			}
		}

		table.group_table {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			margin: 0;
			border: none;
			padding: 0;

			tbody {
				display: block;
			}

			tr:first-child{
				border-top: 1px solid $default-border-color;
			}

			tr {
				@include eltdfFlexLayout();
				flex-direction: row-reverse;
				flex-flow: nowrap;
				justify-content: space-between;
				align-items: center;
				padding: 0;
				margin: 0;
				border: 1px solid $default-border-color;
				border-top: 0;
				font-size: 14px;

				&:last-child {
					margin: 0;
				}

				td.woocommerce-grouped-product-list-item__quantity {
					order: 3;
					margin: 0;
					padding: 0 12px;

					.eltdf-quantity-buttons {

						.eltdf-quantity-input {
							width: 56px;
							height: 48px;
							left: 1px;
							line-height: 48px;
							margin: 0;
							padding: 0 !important;
							border: 0;
							font-size: 13px;
							font-family: $default-text-font;
							font-weight: 300;
						}

						.eltdf-quantity-minus {
							position: absolute;
							top: 50%;
							left: 0;
							@include eltdfTransform(translateY(-50%));
							border: 0;
							z-index: 100;

							&:before{
								content: "\34";
								font-size: 15px;
							}
						}

						.eltdf-quantity-plus {
							position: absolute;
							top: 50%;
							right: 0;
							@include eltdfTransform(translateY(-50%));
							border: 0;
							z-index: 100;

							&:before{
								content: "\35";
								font-size: 15px;
							}
						}
					}
				}
			}

			td.woocommerce-grouped-product-list-item__label {
				order: 1;
				margin: 0;
				padding: 0 20px;
				width: 30%;
				text-align: left;
			}

			td.woocommerce-grouped-product-list-item__price{
				order: 2;
				margin: 0;
				padding: 0;
			}

			tr:last-child {
				border-bottom: 1px solid $default-border-color;
				margin-bottom: 20px;
			}

			p.stock.in-stock, p.stock.out-of-stock {
				display: inline;
			}

			.eltdf-quantity-buttons {
				margin: 0;
			}
		}

		.variations {
			position: relative;
			display: table;
			vertical-align: middle;
			margin: 0;
			border: none;
			padding: 0;

			tr {
				border: 1px solid $default-border-color;
				line-height: 48px;

				&:last-child {
					margin: 0;
				}
			}

			td {
				padding: 0;
				text-align: initial;

				&.label {
					color: #464646;
					padding: 0 20px;
				}

				&.value {
					color: #464646;
					white-space: nowrap;
					padding: 0 12px;
					text-align: right;

					select {
						width: auto;
						min-width: 200px;
					}

					.select2-container--default{
						color: #464646;
						margin: 0;
						font-size: 14px;
						text-transform: capitalize;
						letter-spacing: 0;
						font-weight: 300;
					}
				}
			}

			.reset_variations {
				margin: 0 0 0 15px;
				color: $default-heading-color;
				display: none!important;
			}
		}

		.single_variation_wrap {
			margin: 35px 0 0;

			.single_variation {
				margin: 0 0 35px;
			}
		}

	}

	.woocommerce-tabs {
		@include eltdfRelativeHolderLayout();
		margin: 34px 0 0;
		
		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
			font-size: 0;

			&:after {
				content: '';
				width: 100%;
				position: absolute;
				left: 0;
				bottom: 0;
				background-color: $default-border-color;
				height: 1px;
				z-index: -1;
			}
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					font-family: $default-heading-font;
					font-size: 17px;
					font-weight: 700;
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 26px;
					@include eltdfAnimationLineHidden($bottom:0, $color:currentColor);
				}

				&:hover,
                &.active {

					a {
						@include eltdfAnimationLineVisible($bottom:0, $color:currentColor);
					}
				}
			}
		}

		.entry-content {
			@include eltdfRelativeHolderLayout();
			min-height: 156px;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 32px 0 20px;
				line-height: 26px;
			}
		}
		
		table {
			margin: 32px 0 20px;
			
			tr, th {
				border: 0;
				font-size: 10px;
				text-transform: uppercase;
				font-weight: 400;
				letter-spacing: .16em;
			}
			
			td, th {
				padding: 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 32px 0 20px;
			
			h2 {
				@include eltdfH3();
				margin: 0 0 51px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 50px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 103px;
						border-radius: 50%;
					}
				}
				
				.comment-text {
					padding: 0 0 0 131px;

					.star-rating{
						float: right;
					}
					
					p {
						margin: 0;

						.woocommerce-review__author{
							font-size: 18px;
							font-weight: 600;
							display: block;
							line-height: 1.33em;
							font-family: $default-heading-font;
							color: $default-heading-color;
						}

						.woocommerce-review__dash{
							display: none;
						}

						.woocommerce-review__published-date{
							display: block;
							font-size: 10px;
							text-transform: uppercase;
							letter-spacing: .16em;
							font-weight: 400;
						}
					}

					.description{
						margin: 12px 0 0;
						line-height: 26px;
					}
				}
			}
			
			.comment-respond {
				margin-top: 113px;
				
				.comment-reply-title {
					@include eltdfH3();
					font-family: $default-heading-font;
					font-weight: 600;
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}

				label{
					font-size: 10px;
					text-transform: uppercase;
					letter-spacing: .16em;
					font-weight: 400;
				}

				.comment-form-rating {
					margin: 17px 0;

					.stars {
						overflow: hidden;
						margin-top: -10px;

						a {
							position: relative;
							display: inline-block;
							color: transparent;
							font-size: 0;
							margin: 0;
							@include eltdfWooCommerceSingleProductRatingsStyle();

							&.active:after {
								bottom: 4px;
							}
						}
					}
				}

				.comment-form-author{
					float: left;
					width: 49%;
					padding-right: 2%;
				}

				.comment-form-email{
					float: left;
					width: 49%;
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2 {
			@include eltdfH3();
			display: block;
			text-align: left;
			margin: 25px 0 53px;
		}
		
		.product {
			
			.eltdf-pl-image {
				img {
					@include eltdfTransform(scale(1.06));
					@include eltdfTransitionTransform(.4s cubic-bezier(0.54, 0.01, 0.58, 1))
				}
			}
			
			.eltdf-pl-text {
				opacity: 1;
				background: transparent;
				
				.eltdf-pl-icons-holder {
					@include eltdfInlineFlexContainer(null, null, row-reverse);
					padding: 0 8px;
					position: absolute;
					bottom: -1px;
					right: -1px;
					background-color: #fff;
					z-index: 111;
					
					@include eltdfTransform(translateX(100%));
					@include eltdfTransition(all 1.1s cubic-bezier(0.38, 0.13, 0.17, 0.84));
					
					> .add_to_cart_button,
					> .product_type_external,
					> .yith-quickview-button,
					> .yith-wcwl-add-to-wishlist,
					.added_to_cart {
						height: 43px;
						width: 18px;
						z-index: 300;
						overflow: hidden;
						padding: 0 8px;
						font-size: 0;
						vertical-align: bottom;
						margin: 0;
						border: 0;
						box-sizing: initial;
						
						&:before, &:after {
							content: initial;
						}
					}
					
					.add_to_cart_button,
					.product_type_external {
						bottom: 0;
						right: -1px;
						display: inline-block;
						background-color: #fff;
						background-image: url("../img/cart-icon.svg");
						background-repeat: no-repeat;
						background-position: center;
						background-size: 17px;
						
						&.added {
							display: none;
						}
					}
					
					.added_to_cart {
						color: unset;
						background-color: unset;
						font-weight: unset;
						
						&:after {
							content: "Z";
							font-size: 18px;
							line-height: 46px;
							font-family: 'ElegantIcons';
						}
					}
					
					> .yith-quickview-button {
						bottom: 0;
						
						&:before {
							content: "\f06e";
							font-size: 17px;
							line-height: 43px;
							font-family: 'FontAwesome';
						}
					}
					
					> .yith-wcwl-add-to-wishlist {
						bottom: 0;
						
						> div {
							height: 100%;
							
							a {
								height: 100%;
								
								&:before {
									font-size: 16px;
									line-height: 45px;
									padding: 0;
									margin: 0;
								}
							}
						}
					}
				}
			}
			
			&:hover {
				.eltdf-pl-image {
					img {
						@include eltdfTransform(translateX(-5px) scale(1.06));
					}
				}
				.eltdf-pl-icons-holder {
					@include eltdfTransform(translateX(0));
				}
			}
			
			.eltdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */