/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        line-height: 1;
        font-family: $default-heading-font;
        font-weight: 600;
        font-size: 14px;
        color: #000;
        letter-spacing: 0.01em;

        &:hover {

            .eltdf-cart-text-holder{
                @include eltdfAnimationLineVisible($bottom:0px, $color: currentColor);
            }
        }

        .eltdf-cart-text-holder {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            line-height: 23px;
            @include eltdfAnimationLineHidden($bottom:0px, $color: currentColor);
        }

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            margin-right: 6px;
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-cart-icon {
                font-size: 22px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;

                }
            }

            .eltdf-cart-number{
                position: absolute;
                display: block;
                top: 50%;
                left:50%;
                @include eltdfTransform(translateY(-50%) translateX(-50%));
            }
        }

        .eltdf-cart-quantity {
            display: inline-block;
            vertical-align: middle;
            &:after {
                content: '/';
                margin: 0 0px 0 3px;
            }
        }

        .eltdf-cart-total-amount {
            display: inline-block;
            vertical-align: middle;
        }
    }

    &:hover {

        .eltdf-shopping-cart-dropdown {
            top: 100%;
            opacity: 1;
            visibility: visible;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 297px;
    background-color: #fff;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    box-shadow: 0 5px 5px 0 rgba(0,0,0,.03);
    @include eltdfTransition(all 0.35s cubic-bezier(.53,.01,.18,1));

    ul {
        margin: 30px;
        padding: 0;

        li {
            @include eltdfTableLayout();
            padding: 0 0 20px;
            box-sizing: border-box;

            &.eltdf-empty-cart {
                color: initial;
                border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 77px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        padding-left: 16px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltdf-quantity, .amount {
            display: inline-block;
            margin: 3px 0 0;
            font-family: $default-text-font;
            font-size: 12px;
            text-transform: uppercase;
            letter-spacing: .16em;
            font-weight: 400;
        }

        .eltdf-quantity{

            &:after{
                content: 'X';
                font-size: 11px;
            }
        }

        .woocommerce-Price-currencySymbol{
            position: relative;
            top: -.5em;
            font-size: 7px;
        }
    }

    .eltdf-cart-bottom {
        border-top: 1px solid #e5e5e5;
        padding-top: 13px;

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 25px;
            font-size: 18px;
            font-weight: 600;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }

            .eltdf-total-amount {
                text-align: right;
	            font-size: 16px;

                .woocommerce-Price-currencySymbol{
                    position: relative;
                    top: -.5em;
                    font-size: 11px;
                }
            }
        }

        .eltdf-view-checkout {
            float: left;
        }

        .eltdf-view-cart {
            float: right;
        }

        .eltdf-view-checkout, .eltdf-view-cart {
            @include eltdfButtonDefaultStyle();
            @include eltdfButtonSize();
            @include eltdfAnimationLineVisible($bottom: 2px, $color: currentColor);
            padding: 0 !important;
            color: $default-text-color !important;
            background-color: transparent !important;
            border: 0;
            letter-spacing: 0.01em;
            vertical-align: middle;

            &:hover {
                @include eltdfAnimationLineHidden($bottom: 2px, $color: currentColor);
            }
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-holder {
        margin: 0;

        .eltdf-shopping-cart-dropdown {
            display: none !important;
        }
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */