/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.eltdf-search-submit),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonSize();
        @include eltdfButtonSolidColor();
        
        @include eltdfAnimationLineVisible($left:-1px, $width: calc(100% + 2px), $bottom:-4px);

        white-space: nowrap;
        cursor: pointer;
        z-index: 3;
        text-transform: capitalize;

        &:hover {
            @include eltdfButtonSolidHoverColor();
            @include eltdfAnimationLineHidden($left:-1px, $width: calc(100% + 2px), $bottom:-4px);
        }
    }
    
    a.button.added {
        display: none;
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */