/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
    .eltdf-woocommerce-page {

    aside.eltdf-sidebar{

        .widget:first-child{
            margin-top: 7px;
        }
    }

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 12px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: $sselect-input-height;
        font-family: $default-text-font;
        font-size: 10px;
        text-transform: uppercase;
        letter-spacing: .160em;
        font-weight: 400;
    }

    .woocommerce-ordering {
        text-align: right;

        @media screen and (max-width: 680px){
            text-align: left;
        }

        .select2-container--default{
            text-align: right;

            @media screen and (max-width: 680px){
                text-align: left;
            }

            .select2-selection__rendered{
                padding-right: 30px;
            }
        }

        .orderby {
            width: auto;
            min-width: calc(66% - 15px);
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */