/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.eltdf-content .woocommerce {
    @include eltdfRelativeHolderLayout();
}

.eltdf-pl-main-holder {
    //margin: 0 0 22px;
    
    .eltdf-woo-pl-info-on-image-hover & {
        margin: 0 0 50px;
    }
}

ul.products {
    list-style: none;
	margin: 0 -15px;
    clear: both;
    
    > .product {
        position: relative;
        display: inline-block;
	    vertical-align: middle;
	    float: left;
	    margin: 0 0 30px;
	    padding: 0 15px;
	    box-sizing: border-box;

        >.yith-wcqv-button{
            display: none !important;
        }
        
        .touch & {
            cursor: pointer;
        }
        
        &:hover {
            
            .eltdf-pl-inner .eltdf-pl-text {
                opacity: 1;
            }
        }
        
        .eltdf-product-list-title {
            float: left;
            width: 55%;
            margin: 0;
            line-height: 21px;

            a{
                font-weight: 500;
            }
        }

        .eltdf-pl-category{
            font-size: 10px;
            text-transform: uppercase;
            font-weight: 500;
            letter-spacing: .160em;
            line-height: 21px;
            float: left;
            width: 100%;
            margin: 4px 0 0;
	        
	        a {
		        position: relative;
		        @include eltdfAnimationLineHidden($bottom:-2px, $color:currentColor);
		        
		        &:hover {
			        @include eltdfAnimationLineVisible($bottom:-2px, $color:currentColor);
		        }
	        }
        }
        
        .price {
            @include eltdfWooCommercePriceStyle();
            font-size: 14px;
            line-height: 21px;
            float: right;
            width: 45%;
            margin: 0;
            text-align: right;
        }

        .eltdf-pl-rating-holder {
            @include eltdfRelativeHolderLayout();
            line-height: 1;
            margin: 10px 0 3px;
        }
        
        .button,
        .added_to_cart {
            margin: 25px 0 0;

            .eltdf-woo-pl-info-below-image & {
                margin: 0;
            }
        }
        
        .button.added {
            display: none;
        }


        .eltdf-pl-inner {
            @include eltdfRelativeHolderLayout();
            overflow: hidden;
	        @include eltdfImageMoveHoverStyle();
            
            .eltdf-pl-image {
                @include eltdfRelativeHolderLayout();
                overflow: hidden;
                
                img {
                    display: block;
                    width: 100%;
                }
            }
            
            .eltdf-pl-text {
                @include eltdfAbsoluteHolderLayout();
                padding: 10px;
                opacity: 0;
                box-sizing: border-box;
                @include eltdfTransition(opacity .2s ease-out);
            }
            
            .eltdf-pl-text-outer {
                @include eltdfTableLayout();
                text-align: center;
            }
            
            .eltdf-pl-text-inner {
                @include eltdfTableCellLayout();
                
                .eltdf-product-list-title,
                .price,
                .star-rating {
                    color: #fff;
                }
                
                .price del {
                    color: rgba(#fff, .8);
                }
            }
            
            > a {
                @include eltdfAbsoluteHolderLayout();
            }

            > .eltdf-pl-text {
                padding: 30px;
                background-color: rgba(#000, 0.6);

                .eltdf-product-list-title{
                    float: initial;
                    text-align: center;
                    width: 100%;
                }

                .price {
                    float: initial;
                    text-align: center;
                    width: 100%;
                }
            }
        }
        
        .eltdf-pl-text-wrapper {
            @include eltdfRelativeHolderLayout();
            margin: 20px 0 38px;
        }
    }
}

.eltdf-woo-pl-info-on-image-hover {
	.eltdf-pl-text {
		background-color: rgba(#fff, 0.6);
	}
}

.eltdf-woo-pl-info-below-image{

    ul.products{

        .eltdf-pl-inner {

            .eltdf-pl-text {
                padding: 0;

                .eltdf-pl-text-inner {
	
	                .eltdf-pl-icons-holder {
		                position: absolute;
		                @include eltdfInlineFlexContainer(null, null, row-reverse);
		                padding: 0 8px;
		                bottom: -1px;
		                right: -1px;
		                background-color: #fff;
		                z-index: 111;
		                @include eltdfTransform(translateX(100%));
		                @include eltdfTransition(transform .35s cubic-bezier(0.34, 0.18, 0, 0.9));
		
		                > a.add_to_cart_button,
		                > a.button.product_type_simple,
                        > a.button.product_type_external,
		                > .yith-quickview-button,
		                > .yith-wcwl-add-to-wishlist,
		                .added_to_cart {
			                height: 43px;
			                width: 18px;
			                z-index: 300;
			                overflow: hidden;
			                padding: 0 8px;
			                color: $first-main-color;
			                font-size: 0;
			                vertical-align: bottom;
			                margin: 0;
			                border: 0;
			                box-sizing: initial;
			                @include eltdfTransition(opacity .2s ease-out);
			
			                &:before, &:after {
				                content: initial;
				                color: inherit;
			                }
			
			                &:hover {
				                opacity: .8;
			                }
		                }
		
		                > a.add_to_cart_button,
		                > a.button.product_type_simple,
		                > a.button.product_type_external {
			                bottom: 0;
			                right: -1px;
			                display: inline-block;
			                background: none;
			                box-sizing: content-box;
			
			                &.added {
				                display: none;
			                }
			
			                &:after {
				                font-family: 'Ionicons';
				                content: "\f110";
				                font-size: 19px;
				                line-height: 42px;
				                font-weight: 500;
			                }
		                }
		                
		                > .added_to_cart {
			                color: unset;
			                background-color: unset;
			                font-weight: unset;
			
			                &:after {
				                content: "Z";
				                font-size: 18px;
				                line-height: 46px;
				                font-family: 'ElegantIcons';
			                }
		                }
		
		                > .yith-quickview-button {
			                bottom: 0;
			
			                &:before {
				                content: "\f06e";
				                font-size: 17px;
				                line-height: 43px;
				                font-family: 'FontAwesome';
			                }
		                }
		
		                > .yith-wcwl-add-to-wishlist {
			                bottom: 0;
			
			                > div {
				                height: 100%;
				
				                a {
					                height: 100%;
					
					                &:before {
						                font-size: 16px;
						                line-height: 45px;
						                padding: 0;
						                margin: 0;
					                }
				                }
			                }
		                }
	                }
                }
            }
	
	        &:hover {
		        .eltdf-pl-text .eltdf-pl-text-inner .eltdf-pl-icons-holder {
				        @include eltdfTransform(translateX(0));
			        }
	        }
        }
    }
}


$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
    &.eltdf-woo-#{nth($columns_space_label,$i+1)}-space {
        $column_width: nth($columns_space_width,$i+1);
        $column_width_margin: $column_width * 2;
        
        ul.products {
            @if($column_width == 0) {
                margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }
            
            > .product {
                padding: 0 #{$column_width}px;
                margin: 0 0 #{$column_width_margin}px;
            }
        }
    }
}

.eltdf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
    
    $product_list_columns: ('columns-3', 'columns-4');
    
    @for $i from 0 to length($product_list_columns) {
        $column_width: 100%/($i+3);
        
        > .products {
            
            > .product {
                
                @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                    
                    .eltdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: $column_width;
                        
                        &:nth-child(#{$i+3}n+1) {
                            clear: both;
                        }
                    }
                }
            }
        }
    }
}

.woocommerce {
    
    &.columns-1 {
        
        > .products {
            
            > .product {
                float: none;
                width: 100%;
            }
        }
    }
    
    $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
    
    @for $i from 0 to length($product_list_vc_columns) {
        &.#{nth($product_list_vc_columns,$i+1)} {
            $column_vc_width: 100%/($i+2);
            
            > .products {
                
                > .product {
                    width: $column_vc_width;
                    $product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
                    
                    @if $product_list_vc_columns_label == 'columns-5' {
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-6' {
                        @media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-3' {
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else if $product_list_vc_columns_label == 'columns-2' {
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    } @else {
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(#{$i+2}n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */